.App {
  text-align: center;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

form {
  margin: 20px auto;
  padding: 30px 20px;
  width: 460px;
  border-radius: 7px;
  backdrop-filter: blur(5px);
  background-color: rgb(35, 114, 217);
  overflow: hidden;
  box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.1);
}
@media only screen and (max-width: 480px) {
  form {
    width: 70vw;
  }
}